import { UniverseChainId } from "uniswap/src/features/chains/types";
import Trace from "uniswap/src/features/telemetry/Trace";
import { sendAnalyticsEvent } from "uniswap/src/features/telemetry/send";
import { useUSDCValue } from "uniswap/src/features/transactions/swap/hooks/useUSDCPrice";
import { NumberType, useFormatter } from "utils/formatNumbers";
const FooterContainer = styled.div`
  padding: 0px 12px;
`;
const Footer = styled.div`
  border-top: 1px solid ${({ theme }) => theme.surface3};
  color: ${({ theme }) => theme.neutral1};
  display: flex;
  flex-direction: column;
  margin: 0px 16px 8px;
  padding: 12px 0px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
`;
const FooterHeader = styled(Column)`
  padding-top: 8px;
  padding-bottom: 16px;
`;
const CurrencyRow = styled(Row)`
  justify-content: space-between;
  align-items: start;
  gap: 8px;
`;
const TotalColumn = styled(Column)`
  text-align: end;
  overflow: hidden;
`;
const WarningIcon = styled(AlertTriangle)`
  width: 14px;
  margin-right: 4px;
  color: inherit;
`;
const WarningText = styled(ThemedText.BodyPrimary)`
  align-items: center;
  color: ${({ $color }) => $color};
  display: flex;
  justify-content: center;
  margin-bottom: 10px !important;
  text-align: center;
`;
const HelperText = styled(ThemedText.BodySmall)`
  color: ${({ $color }) => $color};
  display: flex;
  justify-content: center;
  text-align: center;
  margin-bottom: 10px !important;
`;
const CurrencyInput = styled(Row)`
  gap: 8px;
  cursor: pointer;
`;
const ActionButton = styled.button`
  display: flex;
  background: ${({ $backgroundColor }) => $backgroundColor};
  color: ${({ $color }) => $color};
  font-weight: 535;
  line-height: 24px;
  font-size: 16px;
  gap: 16px;
  justify-content: center;
  border: none;
  border-radius: 12px;
  padding: 12px 0px;
  cursor: pointer;
  align-items: center;

  &:disabled {
    opacity: 0.6;
    cursor: auto;
  }
`;
const FiatLoadingBubble = styled(LoadingBubble)`
  border-radius: 4px;
  width: 4rem;
  height: 20px;
  align-self: end;
`;
const PriceImpactContainer = styled(Row)`
  align-items: center;
  gap: 8px;
  width: 100%;
  justify-content: flex-end;
`;
const PriceImpactRow = styled(Row)`
  align-items: center;
  gap: 8px;
`;
const ValueText = styled(ThemedText.BodyPrimary)`
  line-height: 20px;
  font-weight: 535;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  scrollbar-width: none;

  ::-webkit-scrollbar {
    display: none;
  }
`;
const Warning = ({ color, children }) => {
  if (!children) {
    return null;
  }
  return <WarningText data-testid="nft-buy-button-warning" fontSize="14px" lineHeight="20px" $color={color}><WarningIcon />{children}</WarningText>;
};
const Helper = ({ children, color }) => {
  if (!children) {
    return null;
  }
  return <HelperText lineHeight="16px" $color={color}>{children}</HelperText>;
};
const InputCurrencyValue = ({
  usingPayWithAnyToken,
  totalEthPrice,
  activeCurrency,
  tradeState,
  trade
}) => {
  const { formatEther: formatEther2, formatNumberOrString } = useFormatter();
  if (!usingPayWithAnyToken) {
    return <ThemedText.BodyPrimary lineHeight="20px" fontWeight="535">{formatEther2({ input: totalEthPrice.toString(), type: NumberType.NFTToken })}
        &nbsp;{activeCurrency?.symbol ?? "ETH"}</ThemedText.BodyPrimary>;
  }
  if (tradeState === TradeState.LOADING && !trade) {
    return <ThemedText.BodyPrimary color="neutral3" lineHeight="20px" fontWeight="535"><Trans i18nKey="swap.fetchingPrice" /></ThemedText.BodyPrimary>;
  }
  return <ValueText color={tradeState === TradeState.LOADING ? "neutral3" : "neutral1"}>{formatNumberOrString({ input: trade?.inputAmount.toExact(), type: NumberType.NFTToken })}</ValueText>;
};
const FiatValue = ({
  usdcValue,
  priceImpact,
  tradeState,
  usingPayWithAnyToken
}) => {
  const { t } = useTranslation();
  const { formatNumberOrString } = useFormatter();
  if (!usdcValue) {
    if (usingPayWithAnyToken && (tradeState === TradeState.INVALID || tradeState === TradeState.NO_ROUTE_FOUND)) {
      return null;
    }
    return <FiatLoadingBubble />;
  }
  return <PriceImpactContainer>{priceImpact && <><MouseoverTooltip text={t("swap.estimatedDifference.label")}><PriceImpactRow><AlertTriangle color={priceImpact.priceImpactSeverity.color} size="16px" /><ThemedText.BodySmall style={{ color: priceImpact.priceImpactSeverity.color }} lineHeight="20px">
                ({priceImpact.displayPercentage()})
              </ThemedText.BodySmall></PriceImpactRow></MouseoverTooltip></>}<ThemedText.BodySmall color="neutral3" lineHeight="20px">{`${formatNumberOrString({ input: usdcValue?.toExact(), type: NumberType.FiatNFTToken })}`}</ThemedText.BodySmall></PriceImpactContainer>;
};
const PENDING_BAG_STATUSES = [
  BagStatus.FETCHING_ROUTE,
  BagStatus.CONFIRMING_IN_WALLET,
  BagStatus.FETCHING_FINAL_ROUTE,
  BagStatus.PROCESSING_TRANSACTION
];
export const BagFooter = ({ setModalIsOpen, eventProperties }) => {
  const accountDrawer = useAccountDrawer();
  const theme = useTheme();
  const account = useAccount();
  const isSupportedChain = useIsSupportedChainId(account.chainId);
  const connected = account.isConnected && account.chainId;
  const totalEthPrice = useBagTotalEthPrice();
  const { inputCurrency } = useTokenInput(({ inputCurrency: inputCurrency2 }) => ({ inputCurrency: inputCurrency2 }));
  const setInputCurrency = useTokenInput((state) => state.setInputCurrency);
  const defaultCurrency = useCurrency("ETH");
  const inputCurrencyBalance = useTokenBalance(
    account.address,
    !!inputCurrency && inputCurrency.isToken ? inputCurrency : void 0
  );
  const {
    isLocked: bagIsLocked,
    bagStatus,
    setBagExpanded,
    setBagStatus
  } = useBag(({ isLocked, bagStatus: bagStatus2, setBagExpanded: setBagExpanded2, setBagStatus: setBagStatus2 }) => ({
    isLocked,
    bagStatus: bagStatus2,
    setBagExpanded: setBagExpanded2,
    setBagStatus: setBagStatus2
  }));
  const [tokenSelectorOpen, setTokenSelectorOpen] = useState(false);
  const isPending = PENDING_BAG_STATUSES.includes(bagStatus);
  const activeCurrency = inputCurrency ?? defaultCurrency;
  const usingPayWithAnyToken = !!inputCurrency && account.chainId === UniverseChainId.Mainnet;
  const { universalRouterAddress, universalRouterAddressIsLoading } = useNftUniversalRouterAddress();
  useSubscribeTransactionState(setModalIsOpen);
  const fetchAssets = useFetchAssets();
  const parsedOutputAmount = useMemo(() => {
    return tryParseCurrencyAmount(formatEther(totalEthPrice.toString()), defaultCurrency ?? void 0);
  }, [defaultCurrency, totalEthPrice]);
  const {
    state: tradeState,
    trade,
    maximumAmountIn,
    allowedSlippage
  } = useDerivedPayWithAnyTokenSwapInfo(usingPayWithAnyToken ? inputCurrency : void 0, parsedOutputAmount);
  const allowance = usePermit2Allowance(
    maximumAmountIn,
    getURAddress(isSupportedChain ? account.chainId : void 0, universalRouterAddress),
    TradeFillType.Classic
  );
  const loadingAllowance = allowance.state === AllowanceState.LOADING || universalRouterAddressIsLoading;
  usePayWithAnyTokenSwap(trade, allowance, allowedSlippage);
  const priceImpact = usePriceImpact(trade);
  const fiatValueTradeInput = useUSDCValue(trade?.inputAmount);
  const fiatValueTradeOutput = useUSDCValue(parsedOutputAmount);
  const usdcValue = usingPayWithAnyToken ? fiatValueTradeInput : fiatValueTradeOutput;
  const nativeCurrency = useCurrency(NATIVE_CHAIN_ID);
  const nativeCurencyBalance = useCurrencyBalance(account.address ?? void 0, nativeCurrency);
  const sufficientBalance = useMemo(() => {
    if (!connected || account.chainId !== UniverseChainId.Mainnet) {
      return void 0;
    }
    if (inputCurrency) {
      const inputAmount = trade?.inputAmount;
      if (!inputCurrencyBalance || !inputAmount) {
        return void 0;
      }
      return !inputCurrencyBalance.lessThan(inputAmount);
    }
    if (!nativeCurrency) {
      return void 0;
    }
    const totalEthPriceCurrencyAmount = CurrencyAmount.fromRawAmount(nativeCurrency, JSBI.BigInt(totalEthPrice));
    return nativeCurencyBalance?.greaterThan(totalEthPriceCurrencyAmount);
  }, [
    connected,
    account.chainId,
    inputCurrency,
    nativeCurrency,
    totalEthPrice,
    nativeCurencyBalance,
    trade?.inputAmount,
    inputCurrencyBalance
  ]);
  useEffect(() => {
    setBagStatus(BagStatus.ADDING_TO_BAG);
  }, [inputCurrency, setBagStatus]);
  const switchChain = useSwitchChain();
  const {
    buttonText,
    buttonTextColor,
    disabled,
    warningText,
    warningTextColor,
    helperText,
    helperTextColor,
    handleClick,
    buttonColor
  } = useMemo(() => {
    if (connected && account.chainId !== UniverseChainId.Mainnet) {
      const handleClick3 = () => switchChain(UniverseChainId.Mainnet);
      return getBuyButtonStateData(BuyButtonStates.NOT_SUPPORTED_CHAIN, theme, handleClick3);
    }
    if (sufficientBalance === false) {
      return getBuyButtonStateData(BuyButtonStates.INSUFFICIENT_BALANCE, theme);
    }
    if (bagStatus === BagStatus.WARNING) {
      return getBuyButtonStateData(BuyButtonStates.ERROR, theme);
    }
    if (!connected) {
      const handleClick3 = () => {
        accountDrawer.open();
        setBagExpanded({ bagExpanded: false });
      };
      return getBuyButtonStateData(BuyButtonStates.WALLET_NOT_CONNECTED, theme, handleClick3);
    }
    if (bagStatus === BagStatus.FETCHING_FINAL_ROUTE || bagStatus === BagStatus.CONFIRMING_IN_WALLET) {
      return getBuyButtonStateData(BuyButtonStates.IN_WALLET_CONFIRMATION, theme);
    }
    if (bagStatus === BagStatus.PROCESSING_TRANSACTION) {
      return getBuyButtonStateData(BuyButtonStates.PROCESSING_TRANSACTION, theme);
    }
    if (usingPayWithAnyToken && tradeState !== TradeState.VALID) {
      if (tradeState === TradeState.INVALID) {
        return getBuyButtonStateData(BuyButtonStates.INVALID_TOKEN_ROUTE, theme);
      }
      if (tradeState === TradeState.NO_ROUTE_FOUND) {
        return getBuyButtonStateData(BuyButtonStates.NO_TOKEN_ROUTE_FOUND, theme);
      }
      return getBuyButtonStateData(BuyButtonStates.FETCHING_TOKEN_ROUTE, theme);
    }
    const allowanceRequired = allowance.state === AllowanceState.REQUIRED;
    const handleClick2 = () => allowanceRequired && allowance.approveAndPermit();
    if (loadingAllowance) {
      return getBuyButtonStateData(BuyButtonStates.LOADING_ALLOWANCE, theme, handleClick2);
    }
    if (allowanceRequired) {
      if (allowance.isApprovalPending) {
        return getBuyButtonStateData(BuyButtonStates.IN_WALLET_ALLOWANCE_APPROVAL, theme, handleClick2);
      } else if (allowance.isApprovalLoading) {
        return getBuyButtonStateData(BuyButtonStates.PROCESSING_APPROVAL, theme, handleClick2);
      } else {
        return getBuyButtonStateData(BuyButtonStates.REQUIRE_APPROVAL, theme, handleClick2);
      }
    }
    if (bagStatus === BagStatus.CONFIRM_QUOTE) {
      return getBuyButtonStateData(BuyButtonStates.CONFIRM_UPDATED_PRICE, theme, fetchAssets);
    }
    if (priceImpact && priceImpact.priceImpactSeverity.type === "error") {
      return getBuyButtonStateData(
        BuyButtonStates.PRICE_IMPACT_HIGH,
        theme,
        fetchAssets,
        usingPayWithAnyToken,
        priceImpact
      );
    }
    return getBuyButtonStateData(BuyButtonStates.PAY, theme, fetchAssets, usingPayWithAnyToken);
  }, [
    connected,
    account.chainId,
    sufficientBalance,
    bagStatus,
    usingPayWithAnyToken,
    tradeState,
    loadingAllowance,
    allowance,
    priceImpact,
    theme,
    fetchAssets,
    switchChain,
    accountDrawer,
    setBagExpanded
  ]);
  const traceEventProperties = {
    usd_value: usdcValue?.toExact(),
    using_erc20: !!inputCurrency,
    ...eventProperties
  };
  return <FooterContainer><Footer><FooterHeader gap="xs"><CurrencyRow><Column gap="xs">{isSupportedChain && <><ThemedText.SubHeaderSmall><Trans i18nKey="swap.payWith" /></ThemedText.SubHeaderSmall><CurrencyInput
    onClick={() => {
      if (!bagIsLocked) {
        setTokenSelectorOpen(true);
        sendAnalyticsEvent(NFTEventName.NFT_BUY_TOKEN_SELECTOR_CLICKED);
      }
    }}
  ><CurrencyLogo currency={activeCurrency} size={24} /><ThemedText.HeadlineSmall fontWeight={535} lineHeight="24px">{activeCurrency?.symbol}</ThemedText.HeadlineSmall><ChevronDown size={20} color={theme.neutral2} /></CurrencyInput></>}</Column><TotalColumn gap="xs"><ThemedText.SubHeaderSmall><Trans i18nKey="swap.total" /></ThemedText.SubHeaderSmall><InputCurrencyValue
    usingPayWithAnyToken={usingPayWithAnyToken}
    totalEthPrice={totalEthPrice}
    activeCurrency={activeCurrency}
    tradeState={tradeState}
    trade={trade}
  /></TotalColumn></CurrencyRow><FiatValue
    usdcValue={usdcValue}
    priceImpact={priceImpact}
    tradeState={tradeState}
    usingPayWithAnyToken={usingPayWithAnyToken}
  /></FooterHeader><Trace
    logPress
    eventOnTrigger={NFTEventName.NFT_BUY_BAG_PAY}
    element={InterfaceElementName.NFT_BUY_BAG_PAY_BUTTON}
    properties={traceEventProperties}
    logImpression={connected && !disabled}
  ><Warning color={warningTextColor}>{warningText}</Warning><Helper color={helperTextColor}>{helperText}</Helper><ActionButton
    data-testid="nft-buy-button"
    onClick={handleClick}
    disabled={disabled || isPending}
    $backgroundColor={buttonColor}
    $color={buttonTextColor}
  >{isPending && <Loader size="20px" stroke="white" />}{buttonText}</ActionButton></Trace></Footer><CurrencySearchModal
    isOpen={tokenSelectorOpen}
    onDismiss={() => setTokenSelectorOpen(false)}
    onCurrencySelect={(currency) => {
      setInputCurrency(currency.isNative ? void 0 : currency);
      if (currency.isToken) {
        sendAnalyticsEvent(NFTEventName.NFT_BUY_TOKEN_SELECTED, {
          token_address: currency.address,
          token_symbol: currency.symbol
        });
      }
    }}
    selectedCurrency={activeCurrency ?? void 0}
  /></FooterContainer>;
};
