"use strict";
import { OpacityHoverState } from "components/Common/styles";
import styled from "lib/styled-components";
import { BagCloseIcon } from "nft/components/icons";
import { useMemo } from "react";
import { Trans } from "react-i18next";
import { ButtonText, ThemedText } from "theme/components";
const ClearButton = styled(ButtonText)`
  color: ${({ theme }) => theme.neutral2};
  cursor: pointer;
  font-weight: 535;
  font-size: 14px;
  line-height: 16px;

  :active {
    text-decoration: none;
  }
`;
const IconWrapper = styled.button`
  align-items: center;
  background-color: transparent;
  border-radius: 8px;
  border: none;
  color: ${({ theme }) => theme.neutral1};
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-left: auto;
  padding: 2px;
  opacity: 1;

  ${OpacityHoverState}
`;
const CounterDot = styled.div`
  align-items: center;
  background-color: ${({ theme }) => theme.accent1};
  border-radius: 100px;
  font-weight: bold;
  color: ${({ theme }) => theme.deprecated_accentTextLightPrimary};
  display: flex;
  font-size: 10px;
  justify-content: center;
  min-width: ${({ sizing }) => sizing};
  min-height: ${({ sizing }) => sizing};
  padding: 4px 6px;
`;
const Wrapper = styled.div`
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 8px;
  justify-content: flex-start;
  margin: 16px 28px;
  text-align: center;
`;
const BASE_SIZING = 17;
const INCREMENTAL_SIZING = 6;
const getCircleSizing = (numberOfAssets) => {
  const numberOfCharacters = numberOfAssets.toString().length;
  return `${BASE_SIZING + INCREMENTAL_SIZING * numberOfCharacters}px`;
};
export const BagHeader = ({ numberOfAssets, closeBag, resetFlow, isProfilePage }) => {
  const sizing = useMemo(() => getCircleSizing(numberOfAssets), [numberOfAssets]);
  return <Wrapper><ThemedText.HeadlineSmall>{isProfilePage ? <Trans i18nKey="common.sell.label" /> : <Trans i18nKey="nft.bag" />}</ThemedText.HeadlineSmall>{numberOfAssets > 0 && <><CounterDot sizing={sizing}>{numberOfAssets}</CounterDot><ClearButton onClick={resetFlow}><Trans i18nKey="tokens.selector.button.clear" /></ClearButton></>}<IconWrapper onClick={closeBag}><BagCloseIcon data-testid="nft-bag-close-icon" /></IconWrapper></Wrapper>;
};
